/* ====================
 * CONFIGURATION
 * ==================== */

 *,
 *:before,
 *::after {
   margin: 0;
   padding: 0;
   box-sizing: border-box;
 }
 
 /* ====================
  * GENERALS STYLES
  * ==================== */
 
 html {
   background: var(--color-background);
   scroll-behavior: smooth;
   -webkit-tap-highlight-color: transparent;
 }
 
 body {
   width: 100%;
   height: auto;
   min-height: 100vh;
   font-size: 18px;
   font-family: var(--font-family);
   -webkit-font-smoothing: antialiased;
   -moz-osx-font-smoothing: grayscale;
 }
 
 div {
   min-width: 0;
   overflow-wrap: break-word;
 }
 
 img {
   vertical-align: top;
   object-fit: contain;
   object-position: center center;
   font-family: 'Rubik', sans-serif;
   font-size: 1rem;
   font-weight: var(--weight-normal);
   text-align: center;
 }
 
 input:disabled {
   appearance: none;
   -webkit-appearance: none;
 }
 
 button {
   appearance: none;
   -webkit-appearance: none;
 }
 
 select {
   appearance: none;
   -webkit-appearance: none;
 }
 
 hr {
   box-sizing: content-box;
 }
 
 ul {
   list-style: none;
 }
 
 a {
   text-decoration: none;
 }
 
 /* ====================
  * FONTS
  * ==================== */
 
 :is(h1, h2, h3, h4, h5, h6) {
   margin-top: 0;
 }
 
 /* ====================
  * FLEX
  * ==================== */
 
 .flex { display: -webkit-flex; display: flex; }
 
 /* Big Size - PC & TV */
 /* h = horizontal :: v = vertical */
 
 .row-wrap { -webkit-flex-flow: row wrap; flex-flow: row wrap; }
 .row-nowrap { -webkit-flex-flow: row nowrap; flex-flow: row nowrap; }
 .column-wrap { -webkit-flex-flow: column wrap; flex-flow: column wrap; }
 .column-nowrap { -webkit-flex-flow: column nowrap; flex-flow: column nowrap; }
 
 .row-h-center { -webkit-justify-content: center; justify-content: center; }
 .row-v-center { -webkit-align-items: center; align-items: center; }
 .row-h-start { -webkit-justify-content: flex-start; justify-content: flex-start; }
 .row-v-start { -webkit-align-items: flex-start; align-items: flex-start; }
 .row-h-end { -webkit-justify-content: flex-end; justify-content: flex-end; }
 .row-v-end { -webkit-align-items: flex-end; align-items: flex-end; }
 .row-h-between { -webkit-justify-content: space-between; justify-content: space-between; }
 .row-h-around { -webkit-justify-content: space-around; justify-content: space-around; }
 .row-h-evenly { -webkit-justify-content: space-evenly; justify-content: space-evenly; }
 .column-h-center { -webkit-align-items: center; align-items: center; }
 .column-v-center { -webkit-justify-content: center; justify-content: center; }
 .column-h-start { -webkit-align-items: flex-start; align-items: flex-start; }
 .column-v-start { -webkit-justify-content: flex-start; justify-content: flex-start; }
 .column-h-end { -webkit-align-items: flex-end; align-items: flex-end; }
 .column-v-end { -webkit-justify-content: flex-end; justify-content: flex-end; }
 
 /* Medium Size - Tablets */
 
 @media screen and (max-width: 700px) { /*992*/
   .md-column-wrap { -webkit-flex-flow: column wrap; flex-flow: column wrap; }
   .md-column-nowrap { -webkit-flex-flow: column nowrap; flex-flow: column nowrap; }
   .md-row-wrap-reverse { -webkit-flex-flow: row-reverse wrap; flex-flow: row-reverse wrap; }
   .md-row-nowrap-reverse { -webkit-flex-flow: row-reverse nowrap; flex-flow: row-reverse nowrap; }
   .md-column-wrap-reverse { -webkit-flex-flow: column-reverse wrap; flex-flow: column-reverse wrap; }
   .md-column-nowrap-reverse { -webkit-flex-flow: column-reverse nowrap; flex-flow: column-reverse nowrap; }
 
   .md-row-h-center { -webkit-justify-content: center; justify-content: center; }
   .md-row-v-center { -webkit-align-items: center; align-items: center; }
   .md-row-h-start { -webkit-justify-content: flex-start; justify-content: flex-start; }
   .md-row-v-start { -webkit-align-items: flex-start; align-items: flex-start; }
   .md-row-h-end { -webkit-justify-content: flex-end; justify-content: flex-end; }
   .md-row-v-end { -webkit-align-items: flex-end; align-items: flex-end; }
   .md-row-h-between { -webkit-justify-content: space-between; justify-content: space-between; }
   .md-row-h-around { -webkit-justify-content: space-around; justify-content: space-around; }
   .md-row-h-evenly { -webkit-justify-content: space-evenly; justify-content: space-evenly; }
   .md-column-h-center { -webkit-align-items: center; align-items: center; }
   .md-column-v-center { -webkit-justify-content: center; justify-content: center; }
   .md-column-h-start { -webkit-align-items: flex-start; align-items: flex-start; }
   .md-column-v-start { -webkit-justify-content: flex-start; justify-content: flex-start; }
   .md-column-h-end { -webkit-align-items: flex-end; align-items: flex-end; }
   .md-column-v-end { -webkit-justify-content: flex-end; justify-content: flex-end; }
 }
 
 /* ====================
  * GRID
  * ==================== */
 
 .grid { display: grid; }
 
 /* Big Size - PC & TV */
 
 .col-1 { grid-template-columns: repeat(1, 1fr); }
 .col-2 { grid-template-columns: repeat(2, 1fr); }
 .col-3 { grid-template-columns: repeat(3, 1fr); }
 .col-4 { grid-template-columns: repeat(4, 1fr); }
 .col-5 { grid-template-columns: repeat(5, 1fr); }
 .col-6 { grid-template-columns: repeat(6, 1fr); }
